<template>
  <div class="container">
    <h4>Message List</h4>
    <br/>
    <div class="row">
      <div class="card list col-4"
        v-for="(message, index) in messages"
        v-bind:index="index"
        v-bind:key="message.id"
      >
        <div class="card-body">
          <label for="message">Message:</label>
          <p class="card-text" v-if="editId !== message.id" v-html="message.content"></p>
          <textarea v-if="editId === message.id"
            type="text"
            :class="{ [`form-control`]:true, [`has-error`]: contentError }"
            v-model="decryptedMessageForEdit"
            name="message"
            v-on:change="handleFieldChange"
          />
          <label for="encryption-type">Encryption Type:</label>
          <p class="card-text" v-if="editId !== message.id">{{ message.encryption_type }}</p>
          <select
            :class="{ [`form-control`]:true, [`has-error`]: encryptionTypeError }"
            v-model="message.encryption_type"
            name="encryption-type"
            v-if="editId === message.id"
            v-on:change="handleFieldChange"
          >
            <option value="backwards">Backwards</option>
            <option value="emo-gize">Emo-gize</option>
            <option value="letter-scramble">Letter-scramble</option>
            <option value="nothing">Nothing</option>
          </select>
          <br/>
          <span>Created on: {{formatDate(message.createdAt)}}</span>
          <br/>
          <a href="#" class="btn btn-primary" v-if="editId !== message.id" @click="editMessage(message.id,index)">Edit</a>&nbsp;
          <a href="#" class="btn btn-primary" v-if="editId === message.id" @click="saveEditedMessage(index)">Save</a>&nbsp;
          <a href="#" class="btn btn-success" v-if="editId !== message.id" @click="readMessage(index)">Read</a>&nbsp;
          <a href="#" class="btn btn-warning" @click="deleteMessage(index)">Delete</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MessageDataService from "../services/MessageDataService";

export default {
  name: "message-list",
  data() {
    return {
      messages: [],
      editId: "",
      decryptedMessageForEdit:"",
      contentError: false,
      encryptionTypeError: false
    };
  },
  methods: {
    formatDate(date) {
      let d = new Date(date);
      let month = '' + (d.getMonth() + 1);
      let day = '' + d.getDate();
      let year = d.getFullYear();

      if (month.length < 2) 
          month = '0' + month;
      if (day.length < 2) 
          day = '0' + day;

      return [year, month, day].join('-');
    },
    retrieveMessages() {
      MessageDataService.getAll()
        .then(response => {
          this.messages = response.data.messages;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },
    editMessage(id,index){
      this.editId = id;
      this.contentError = false;
      this.encryptionTypeError = false;
      this.readMessage(index);
      console.log("Edit");
    },
    handleFieldChange(e){
      switch (e.target.name) {
        case "message":
            if(e.target.value === undefined || (typeof e.target.value === "string" && e.target.value.trim() === "")){
              this.contentError = true;
            }else{
              this.contentError = false;
            }
            break;
        case "encryption-type":
            if(e.target.value === undefined || (typeof e.target.value === "string" && e.target.value.trim() === "")){
              this.encryptionTypeError = true;
            }else{
              this.encryptionTypeError = false;
            }
            break;
        default:
      }
    },
    saveEditedMessage(id){
      var data= this.messages[id];
      data.content = this.decryptedMessageForEdit;
      if(data.content === undefined || (typeof data.content === "string" && data.content.trim() === "")){
        this.contentError = true;
      }
      if(data.encryption_type === undefined ){
        this.encryptionTypeError = true;
      }
      if(this.contentError || this.encryptionTypeError){
        return;
      }
      MessageDataService.update(data)
        .then(response => {
          if(response.data.success){
            alert("Updated Successfully");
            this.editId = "";
            this.decryptedMessageForEdit="";
            data.content = response.data.encrypted_message;
          }else{
            alert("Something went wrong");
          }
        })
        .catch(e => {
          console.log(e);
        });
        console.log("Save Edited");
    },
    readMessage(id){
      var message_id= this.messages[id].id;
      MessageDataService.get(message_id)
        .then(response => {
          if(response.data.success){
            if(!this.editId){
              alert("Decrypted Message "+response.data.decrypted_message);
            }else{
              this.decryptedMessageForEdit = response.data.decrypted_message;
            }
          }else{
            alert("Something went wrong");
          }
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
        console.log("Save Edited");
    },
    deleteMessage(id){
      MessageDataService.delete(this.messages[id].id)
          .then(response => {
            if(response.data.success){
              alert("Deleted Successfully");
              this.messages.splice(id, 1);
            }else{
              alert("Something went wrong");
            }
          })
          .catch(e => {
            console.log(e);
          });
        console.log("Delete");
    },
    refreshList() {
      this.retrieveMessages();
    }
  },
  mounted() {
    this.retrieveMessages();
  }
};
</script>

<style>
.list {
  text-align: left;
  margin:5px;
}
.has-error{
  border:1px solid red;
}
</style>